import 'rxjs/add/operator/map';
import 'rxjs/add/operator/take';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Router, Resolve, RouterStateSnapshot,ActivatedRouteSnapshot } from '@angular/router';

import { ProfessionalService } from './professional.service';


  
@Injectable()

export class ProfessionalResolver implements Resolve<any> {

  constructor(private api : ProfessionalService, private router: Router) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    let name = route.paramMap.get('name');

    return this.api.filter_category(name).take(1).map(res => {
      if (res) {
        return res;
      } else { 
        this.router.navigate(['/view']);
        return null;
      }
    });
  }
}



