import { NgModule }  from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CraftmanComponent } from './craftman.component';
import { LogoComponent } from './logo.component';
import { CompanyProductComponent } from './company_product.component';
import { ProfessionalComponent } from './professionals.component';
import { EquipmentComponent } from './equipments.component';
import { IndexComponent } from './indexes.component';
 
const CraftRoutes : Routes = [
  {path: '', component: CraftmanComponent,
    children: [
      {path:'', component:IndexComponent,
        children: [
        {path:':id',component:LogoComponent}
        ]
      },
      {path:'product/:id', component:CompanyProductComponent},
      {path:'professionalin/:id', component:ProfessionalComponent},
      {path:'rental_equipment/:id', component:EquipmentComponent}
    ]},


]

@NgModule ({
	imports:[RouterModule.forChild(CraftRoutes)],
	exports:[RouterModule]
})

export class CraftmanRouting { }


