import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'tile',
  templateUrl: './tile.component.html',
  styleUrls: ['./tile.component.css']
})
export class TileComponent implements OnInit {

tile :number;
white :number;
portland :number;
sub_one :number;
waste :number;
sub_two :number;
transport :number;
sub_three :number;
labour :number;
sub_four :number;
overhead :number;
profit :number;
rate :number;


  constructor() { }

  ngOnInit() {
  }

BlockForm = new FormGroup ({
  tile : new FormControl (),
  white : new FormControl (),
  govt : new FormControl (),
  portland : new FormControl (),
  overhead : new FormControl (),
  profit : new FormControl (),
  transport : new FormControl ()

})


solution () {
	
let t = this.BlockForm.value.tile;
let wc = this.BlockForm.value.white;
let pw = this.BlockForm.value.portland;
let g = this.BlockForm.value.govt;
let o = this.BlockForm.value.overhead/100;
let p = this.BlockForm.value.profit/100;
let tr = this.BlockForm.value.transport/100;

this.tile = 10 * t;
this.white = 0.25* wc;
this.portland = 0.5* pw;
this.sub_one = +this.tile + +this.white + +this.portland;
this.waste = 0.05 * this.sub_one;
this.sub_two = +this.sub_one + +this.waste;
this.transport = 0.1 * this.sub_two;
this.sub_three = +this.sub_two + +this.transport;
this.labour = (1.81 * 2 * g /25 )* 0.78;
this.sub_four = +this.sub_three + +this.labour;
this.overhead = o * this.sub_three;
this.profit = p * this.sub_four;
this.rate = (+this.sub_four + +this.overhead + +this.profit) / 10
console.log(this.rate)
}

}



