import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ProductService } from '../add_product.service';
import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'exist-manu',
  template: `

<div class="container">
<div class="row">
    <div class="col-md-8">
   <form [formGroup]="ManufacturerForm" (ngSubmit)="onSubmit()">
  
  <select class="form-control" formControlName="manufacturer">
  <option *ngFor="let manufacturer of manufacturer" [ngValue]="manufacturer.id">{{manufacturer.company_name}}</option>
  </select>

<div class="form-group">
       <input type="submit" value="Register">
   </div>

  </form>
  </div>
  </div> 
</div>


  `,
  styleUrls: ['../product.component.css']
})
export class ExistManufacturerComponent implements OnInit {

 constructor(private api : ProductService,private router : Router) { }

  ngOnInit() {
 this.get_manufacturer ()
  }

  manufacturer = [];

  ManufacturerForm = new FormGroup ({
  manufacturer : new FormControl()
  })

get_manufacturer () { 
  this.api.manufactures ()
  .subscribe(res => {this.manufacturer = res }) 
}

onSubmit () {

 const id = this.ManufacturerForm.value;
  this.router.navigate(['/manu-form/manu-product',id.manufacturer]);
}
}



