import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'block',
  templateUrl: './block.component.html',
  styleUrls: ['./block.component.css']
})
export class BlockworkComponent implements OnInit {
 
cement :number;
sand :number;
h_cement:number;
h_sand :number;
sub_1 :number;
mortar :number;
block :number;
sub_2 :number;
wastage :number;
skilled :number;
unskilled :number;
sub_3 :number;
overhead :number;
profit :number;
rate : number;

  constructor() { }

  ngOnInit() { 
  }



BlockForm = new FormGroup ({
	cement : new FormControl (),
	block : new FormControl (),
	sand : new FormControl (),
	overhead : new FormControl (),
	profit : new FormControl (),
	wage : new FormControl ()

})


solution () {
	

let x = this.BlockForm.value.cement;
let y = this.BlockForm.value.block;
let z = this.BlockForm.value.sand;
let o = this.BlockForm.value.overhead;
let p = this.BlockForm.value.profit;
let w = this.BlockForm.value.wage;

this.cement = 9 * x;
this.sand = 1.2 * z;
this.h_cement = 9 * 16 * 12;
this.h_sand = 1.2 * 237 * 12;
this.sub_1 = +this.cement + +this.sand + +this.h_cement + +this.h_sand;
this.mortar = this.sub_1 * 0.2;
this.block = y * 90;
this.sub_2 = +this.mortar + +this.block;
this.wastage = 0.05 * this.sub_2;
this.skilled = 1.81 * 2 * (w / 25) * 1.5;
this.unskilled = +this.wastage / 2;
this.sub_3 = +this.sub_2 + +this.wastage + +this.skilled + +this.unskilled;
this.overhead = o * +this.sub_3;
this.profit = p * +this.sub_3;

this.rate = (+this.sub_3 + +this.overhead + +this.profit) / 10

console.log (this.rate)

}


}



