import { NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import { ReactiveFormsModule,FormsModule } from '@angular/forms';
import { AdminRouting } from './admin.routing';
import { AdminComponent } from './admin.component';
import { FormOneComponent } from './invoices/form_1.component';
import { FormTwoComponent } from './invoices/form_2.component';
import { GenerateComponent } from './invoices/generate.component';


@NgModule ({
    declarations: [AdminComponent,FormOneComponent,FormTwoComponent,GenerateComponent],
	imports : [CommonModule,AdminRouting,ReactiveFormsModule,FormsModule],
	exports : []
})

export class AdminModule { }



