import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import {CommonModule} from '@angular/common';
import { ReactiveFormsModule,FormsModule } from '@angular/forms';
import { CraftmanRouting } from './craftman.routing';
import { CraftmanComponent } from './craftman.component';
import { LogoComponent } from './logo.component';
import { CompanyProductComponent } from './company_product.component';
import { ProfessionalComponent } from './professionals.component';
import { EquipmentComponent } from './equipments.component';
import { IndexComponent } from './indexes.component';

@NgModule ({
	declarations:[CraftmanComponent,LogoComponent,CompanyProductComponent,ProfessionalComponent,EquipmentComponent,IndexComponent],
	imports:[HttpModule,CommonModule,CraftmanRouting,ReactiveFormsModule,FormsModule],
	exports:[]

})

export class CraftmanModule { }


