import { Component, OnInit } from '@angular/core';
 import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'hardcore',
  template: `
 
<div class="row">
<div class="col-md-11 main">

<h4> Unit Rate Formula </h4>

<form [formGroup]="HardForm" class="form-horizontal" (ngSubmit)="solution()">

<div class="row">
<div class="col-md-7 insert">
   <div class="form-group">
    <label  class="col-sm-6 control-label">Stone price per meter cubic</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="stone">
    </div>
   </div>

     <div class="form-group">
    <label class="col-sm-6 control-label">Govt Basic Wages per month</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="wage">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Total Cost of tools purchased </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="tool">
    </div>
   </div>
   
     <div class="form-group">
    <label  class="col-sm-6 control-label"> Overhead Expenses</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="overhead">
    </div>
    </div>

    <div class="form-group">
    <label  class="col-sm-6 control-label">days </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="day">
    </div>
   </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Profit </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="profit">
    </div>
   </div>
   

</div>  

<div class="col-md-5 answer">

<p>  <button type="submit" class="btn btn-primary">CALCULATE</button></p>
<p> Unit Rate Per Metre Cubic </p>
<p> {{rate}} </p>


</div>
</div>
</form>
</div>
</div>

  `,
  styleUrls: ['./foundation.component.css']
})
export class HardcoreComponent implements OnInit {

stone : number;
replacement : number;
sub_1 : number;
average : number;
incidental : number;
sub_2 : number;
sub_3 : number;
wastage : number;
labour : number;
tool : number;
sub_4 : number;
overhead : number;
profit : number;
rate : number;

  constructor() { }

  ngOnInit() {
  }


HardForm = new FormGroup ({
  stone : new FormControl (),
  wage : new FormControl (),
  tool : new FormControl (),
  overhead : new FormControl (),
  profit : new FormControl (),
  day : new FormControl ()
})


solution() {
  
let x = this.HardForm.value.stone;
let w = this.HardForm.value.wage; 
let t = this.HardForm.value.tool;
let o = this.HardForm.value.overhead/100;
let p = this.HardForm.value.profit/100;
let d = this.HardForm.value.day;

this.stone = +1.25 * x;
this.replacement = +0.1 * t;
this.sub_1 = +t + +this.replacement;
this.average = +this.sub_1 / 2;
this.incidental = 0.15 * +d;
this.sub_2 = +d + +this.incidental;
this.sub_3 = +this.sub_1 / +this.sub_2;
this.wastage = 0.05 * +this.stone;
this.labour = (1.81 * ( +w / 25) * +0.25);
this.tool = +this.sub_3 * 0.25;
this.sub_4 = +this.stone + +this.wastage + +this.labour + +this.tool;
this.overhead = o * +this.sub_4;
this.profit = p * +this.sub_4;

this.rate = (+this.sub_4 + +this.overhead + +this.profit) 
console.log (this.rate)

}

}



