import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'plastering',
  template: `


<div class="row">
<div class="col-md-11 main">

<h4> Unit Rate Formula </h4>


</div>
</div>

<div class="row">
<form [formGroup]="BlockForm" class="form-horizontal" (ngSubmit)="solution()">  
<div class="col-md-7 insert">


   <div class="form-group">
    <label  class="col-sm-6 control-label">Cement Price per bag</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="cement">
    </div>
   </div>


    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Sand price per meter cubic </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="sand">
    </div>
   </div>
   
     <div class="form-group">
    <label  class="col-sm-6 control-label"> Overhead Expenses</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="overhead">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Profit </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="profit">
    </div>
   </div>
   
     <div class="form-group">
    <label  class="col-sm-6 control-label">Govt Basic Wages per month </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="wage">
    </div>
   </div>


</div>	

<div class="col-md-5 answer">

<p>  <button type="submit" class="btn btn-primary">CALCULATE</button></p>
<p> Unit Rate Per Metre Square </p>
<p> {{rate}}

</div>
</form>
</div>







  `,
  styleUrls: ['./plaster.component.css']
})
export class PlasteringComponent implements OnInit {

cement :number;
sand  :number;
sub_one :number;
waste :number;
sub_two :number;
skilled :number;
unskilled :number;
sub_three :number;
overhead :number;
profit :number;
rate:number;

  constructor() { }

BlockForm = new FormGroup ({
	cement : new FormControl (),
	sand : new FormControl (),
	overhead : new FormControl (),
	profit : new FormControl (),
	wage : new FormControl ()

})

  ngOnInit() {


  }


  solution () {

let x = this.BlockForm.value.cement; 
let y = this.BlockForm.value.sand; 
let g = this.BlockForm.value.wage;
let o = this.BlockForm.value.overhead/100;
let p = this.BlockForm.value.profit/100;


this.cement =  0.2 * x;
this.sand  = 0.03 * y;
this.sub_one = +this.cement + +this.sand;
this.waste =  0.05 * this.sub_one;
this.sub_two = +this.sub_one + +this.waste;
this.skilled = ((1.81 * 2 * (g /25)) / 8) * 0.75;
this.unskilled = 0.5 * +this.skilled;
this.sub_three = +this.sub_two + +this.skilled + +this.unskilled;
this.overhead = o * this.sub_three;
this.profit = p * this.sub_three;

this.rate = (+this.sub_three + +this.overhead + +this.profit)
console.log(this.rate)



  }

}



