  public address;
  public person;
  public email;
  public phone;
  public regions;
  public district;
  public profile;

 type_pro:any;
  prolism =null;
  arts = null;
  cons = null;
  engin = null;



  constructor(private api : CraftmanService,private router : Router) { }

  ngOnInit() {
  this.get_company_type ()
  }
 
 CraftmanForm = new FormGroup ({
  type : new FormControl (),
  name : new FormControl ('rfd'),
  address : new FormControl(),
  email : new FormControl (),
  person : new FormControl(),
  phone : new FormControl (),
  district : new FormControl(),
  regional : new FormControl(),
  profile : new FormControl(),
  professional : new FormControl(),
  sub_professional : new FormControl()
 });



 get_company_type () {
this.api.company_type() 
.subscribe(res => { this.types = res})
 }


 onSubmit () {
 console.log(this.CraftmanForm.value)
 this.api.store_company(this.CraftmanForm.value)
 .subscribe (res => { 
const id = res
console.log(res)
  if (id > 0) {
    this.router.navigate([id]) 
  } else { console.log('no access') }

})

 }

status (event) {
 console.log(event)
  this.content = event.target.value
 let pro = 11;
  console.log(this.content)
if ( +this.content == +pro ) {
   this.show_professional='true';
} else { this.show_professional=null;}

  console.log(this.type_pro)

 switch (this.type_pro) {

     case 11 :
    this.prolism ='true';
     break;

     default :
      this.prolism = null;
      break

 }


}

  togglepop () {
   this.showState = (this.showState === 'inactive') ? 'active' : 'inactive';
  }





  prof (event) {
    console.log(this.pro)
    switch (this.pro) {
      case this.qs :
  this.arts = null;
  this.cons = null;
  this.engin = null;
        break;

    case this.artin :
  this.arts = 'true';
  this.cons = null;
  this.engin = null;
        break;

     case this.contrator :
  this.arts = null;
  this.cons = 'true';
  this.engin = null;
        break;

    case this.consultant :
  this.arts = null;
  this.cons = null;
  this.engin = 'true';
        break;
    }
  }


