export class GenerateComponent implements OnInit {

  constructor(private api : InvoiceService,private route : ActivatedRoute) { }

 id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {
   this.get_data()
   this.get_client()
  }

 data = [];
 company = [];

 get_data () {
	this.api.get_invoices(this.id)
	.subscribe( res =>this.data = res)
 }

get_client () {
	this.api.get_company(this.id)
	.subscribe(res => this.company = res)
}


 download () { 
 const elementToPrint = document.getElementById('contener2');
const pdf = new jsPDF('p', 'pt', 'a4');
pdf.addHTML(elementToPrint, () => {
    pdf.save('web.pdf');
});
}


}



