import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import { ProductService } from '../add_product.service';
 
@Component({
  selector: 'new-supplier',
  template: `
 <div class="container">
<div class="row"> 
    <div class="col-md-8">
    
   
    <form [formGroup]="SupplierForm" (ngSubmit)="onSubmit()">
   
 <div class="form-group">
    <label>Locations </label>
    <select formControlName="location">
       <option *ngFor="let location of locations" [ngValue]="location.id">{{location.location_name}} </option>
    </select>
  </div>
    


    <div class="form-group">
    <label>Company</label>
    <input type="text" class="form-control" placeholder="Company Name" formControlName="name">
  </div>
  
    <div class="form-group">
    <label>Address</label>
    <input type="text" class="form-control" placeholder="Company Name" formControlName="address">
  </div>


    <div class="form-group">
    <label>Contact person</label>
    <input type="text" class="form-control" placeholder="contact person" formControlName="person">
  </div>

    <div class="form-group">
    <label>Email</label>
    <input type="email" class="form-control" placeholder="email" formControlName="email">
  </div>


      <div class="form-group">
    <label>Contact Phone</label>
    <input type="integer" class="form-control" placeholder="contact phone" formControlName="phone">
  </div>

<textarea class="form-control" rows="3" formControlName="profile"></textarea>


 <div class="form-group">
       <input type="submit" value="Register">
      </div>



</form>

   </div>
  </div>
</div>


  `,
  styleUrls: ['../product.component.css']
})
export class NewSupplierComponent implements OnInit {

 locations  =  [];

  constructor(private api: ProductService,private router : Router) { }

  ngOnInit() {
    this.get_locations ()
  }

  SupplierForm = new FormGroup ({

  name : new FormControl (),
  address : new FormControl (),
  email : new FormControl (),
  person : new FormControl(),
  phone : new FormControl (),
  profile : new FormControl ()
  })


get_locations () {
    this.api.supplier_location()
    .subscribe(res => {this.locations = res})
  }

onSubmit () {
  
  console.log(this.SupplierForm.value)
  this.api.new_supplier(this.SupplierForm.value)
  .subscribe(res => 
   {
  const id = res
  console.log(res)

 if (id > 0) {

  this.router.navigate(['/supplier-form/supplier_logo',id]);

 } else { console.log('no access') }
 })



}

}



