import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';


@Component({
  selector: 'index',
  template: ` 
<div class="container">
<div class="row rental">
<div class="col-md-12">


<a class="btn btn-default" role="button" [routerLink]="['/view/rental',id]" routerLinkActive="">Company Profile </a>
<a class="btn btn-default" role="button" [routerLink]="['equipment',id]" routerLinkActive="">Equipments</a>
<a class="btn btn-default" role="button" [routerLink]="['small']" routerLinkActive="">News </a>
<a class="btn btn-default" role="button" [routerLink]="['large']" routerLinkActive="">Contacts </a>

<router-outlet> </router-outlet>

</div>
</div> 
</div>
  `,
  styleUrls: ['./rental.component.css']
})

export class IndexRentalComponent implements OnInit {

  constructor(private route : ActivatedRoute) { }

  id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {

  }
 
 

}



