import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable ()

export class CraftmanService { 

 constructor (private http : Http) { }

private get_company_typeUrl = "http://127.0.0.1:8000/api/types-company";


private new_companyUrl =  "http://127.0.0.1:8000/api/store-company";

private uploadUrl = "http://127.0.0.1:8000/api/upload-logo";
private galleryUrl = "http://127.0.0.1:8000/api/pro-gallery";
private typeUrl = "http://127.0.0.1:8000/api/select-types";

private product_categoryUrl = "http://127.0.0.1:8000/api/categories";
 
private productUrl ="http://127.0.0.1:8000/api/products-categories";

private serialUrl = "http://127.0.0.1:8000/api/new_serial";

private equipmentUrl = "http://127.0.0.1:8000/api/new-equipment";
private equipment_typeUrl = "http://127.0.0.1:8000/api/types-equip";

company_type () {
   return this.http.get(this.get_company_typeUrl)
   .map((response:Response) => response.json());
}


store_company (data) {
    let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_companyUrl,JSON.stringify(data),options)
	.map((response:Response) => response.json());
}


types (id) { 
   return this.http.get(this.typeUrl+'/'+id)
   .map((response:Response) => response.json());
}

equipment_type () {
	   return this.http.get(this.equipment_typeUrl)
   .map((response:Response) => response.json());
}



upload(id,data) {
   return this.http.post(this.uploadUrl+'/'+id,data)
   .map((response:Response) => response.json());	
} 

categories () {
	 return this.http.get(this.product_categoryUrl)
   .map((response:Response) => response.json());	
}

getproduct(data) {
	return this.http.get(this.productUrl+'/'+data)
   .map((response:Response) => response.json());
}

new_product (id,data) {
	 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.serialUrl+'/'+id,JSON.stringify(data),options)
	.map((response:Response) => response.json());
}

new_equipment (id,data) {
 console.log(id,data)
		 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.equipmentUrl+'/'+id,JSON.stringify(data),options)
	.map((response:Response) => response.json());
}

professional_image (id,data) {
   return this.http.post(this.galleryUrl+'/'+id,data)
   .map((response:Response) => response.json());	
}

} 


