import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RentalComponent } from './rental.component';
import { RentalIndexComponent }  from './rental_index.component';
import { NewRentalComponent } from './new_rental.component';
import { EquipmentRentalComponent } from './equipment_rental.component';
import { RentalsComponent } from './rentals.component';
import { RentalLogoComponent } from './rental_logo.component';

const RentalRoutes: Routes = [ 
 {path:'',component:RentalComponent,
    children: [
     {path:'', component:RentalIndexComponent,
   children:[
        {path:'new', component:NewRentalComponent},
        {path:'exist', component:RentalsComponent},
        {path:'equipment/:id', component:EquipmentRentalComponent},
        {path:'logo/:id', component:RentalLogoComponent}
   ]}
 ]}
];
 

@NgModule ({
	
	imports:[RouterModule.forChild(RentalRoutes)],
	exports:[RouterModule]

})

export class RentalRouting { }



