import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'smallt',
  template: `




<div class="row">
<div class="col-md-11 main">

<h4> Unit Rate Formula </h4>


</div>
</div>

<div class="row">
<form [formGroup]="BlockForm" class="form-horizontal" (ngSubmit)="solution()">  
<div class="col-md-7 insert">


   <div class="form-group">
    <label  class="col-sm-6 control-label">Cement Price per bag</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="cement">
    </div>
   </div>

     <div class="form-group">
    <label class="col-sm-6 control-label">450 x 230 x 150 block price</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="block">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Sand price per meter cubic </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="sand">
    </div>
   </div>
   
     <div class="form-group">
    <label  class="col-sm-6 control-label"> Overhead Expenses</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="overhead">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Profit </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="profit">
    </div>
   </div>
   
     <div class="form-group">
    <label  class="col-sm-6 control-label">Govt Basic Wages per month </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="wage">
    </div>
   </div>


</div>	

<div class="col-md-5 answer">

<p>  <button type="submit" class="btn btn-primary">CALCULATE</button></p>
<p> Unit Rate Per metre square </p>
<p> {{rate}}

</div>
</form>
</div>






  `,
  styleUrls: ['./block.component.css']
})
export class SmallBlockComponent implements OnInit {


cement :number;
sand :number;
h_cement:number;
h_sand :number;
sub_1 :number;
mortar :number;
block :number;
sub_2 :number;
wastage :number;
skilled :number;
unskilled :number;
sub_3 :number;
sub_4 : number;
overhead :number;
profit :number;
rate : number;

  constructor() { }

  ngOnInit() { 
  }



BlockForm = new FormGroup ({
	cement : new FormControl (),
	block : new FormControl (),
	sand : new FormControl (),
	overhead : new FormControl (),
	profit : new FormControl (),
	wage : new FormControl ()

})


solution () {
	

let x = this.BlockForm.value.cement;
let y = this.BlockForm.value.block;
let z = this.BlockForm.value.sand;
let o = this.BlockForm.value.overhead/100;
let p = this.BlockForm.value.profit/100;
let w = this.BlockForm.value.wage;


this.cement = 9 * x;
this.sand = 1.2 * z;
this.h_cement = 9 * 16 * 12;
this.h_sand = 1.2 * 237 * 12;
this.sub_1 = +this.cement + +this.sand + +this.h_cement + +this.h_sand;
this.mortar = this.sub_1 * 0.2;
this.block = y * 90;
this.sub_2 = +this.mortar + +this.block;
this.wastage = 0.05 * this.sub_2;
this.sub_3 = +this.sub_2 + +this.wastage;
this.skilled = (1.81 * 2 * (w / 25) )*1.5;
this.unskilled = +this.skilled * 0.5;
this.sub_4 = +this.sub_3 + +this.skilled + +this.unskilled; 
this.overhead = o * +this.sub_4;
this.profit = p * +this.sub_4;

this.rate = (+this.sub_4 + +this.overhead + +this.profit) / 10

console.log (this.rate)

}


}



