
import { Component,OnInit } from '@angular/core';
import { ProductService } from '../add_product.service';
import { ActivatedRoute,Router } from '@angular/router';
 
 @Component ({
	
	selector:'logo',
	template:`
    <div class="container">
   <div class="row">
   <div class="col-md-12"> 
<label>Logo Company</label>
    <input type="file"   (change)="select($event)">
    <button (click)="upload()"> upload </button>
</div>
</div>
</div>
	 `,
	styleUrls:['../product.component.css']

})

export class SupplierLogoComponent implements OnInit  {

  logo : File = null;
  
  constructor(private api : ProductService, private route : ActivatedRoute,private router : Router) { }
  
  id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {
  }
 
 select (event) {
 this.logo = event.target.files[0];
 }

  upload () {

      
      console.log(this.logo)

      let formData : FormData = new FormData(); 
       formData.append('Logo', this.logo, this.logo.name);  
     this.api.upload_supplier(this.id,formData)
     .subscribe(res => { 

 if (this.id) {

  this.router.navigate(['/supplier-form/supplier-product',this.id]);

 } else { console.log('no access');}

  })

 }

 }
 
 


