import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
@Component({
  selector: 'concreteB',
  template: `



<div class="row">
<div class="col-md-11 main">

<h4> Unit Rate Formula </h4>

</div>
</div>

<div class="row">
<form [formGroup]="BlockForm" class="form-horizontal" (ngSubmit)="solution()">  
<div class="col-md-7 insert">


   <div class="form-group">
    <label  class="col-sm-6 control-label">Cement</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="cement">
    </div>
   </div>

     <div class="form-group">
    <label class="col-sm-6 control-label">Aggregate</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="aggregate">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label"> sand </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="sand">
    </div>
   </div>
   
        <div class="form-group">
    <label  class="col-sm-6 control-label"> FormWork </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="formwork">
    </div>
   </div>

     <div class="form-group">
    <label  class="col-sm-6 control-label"> concrete mixer </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="mixer">
    </div>
   </div>

        <div class="form-group">
    <label  class="col-sm-6 control-label"> total cost of tools </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="tool">
    </div>
   </div>
     <div class="form-group">
    <label  class="col-sm-6 control-label"> personal days  </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="days">
    </div>
   </div>

      <div class="form-group">
    <label  class="col-sm-6 control-label"> govermnent </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="govt">
    </div>
   </div>

     <div class="form-group">
    <label  class="col-sm-6 control-label"> Overhead Expenses</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="overhead">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Profit </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="profit">
    </div>
   </div>
   
        <div class="form-group">
    <label  class="col-sm-6 control-label"> transportation </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="transport">
    </div>
   </div>


</div>	

<div class="col-md-5 answer">

<p>  <button type="submit" class="btn btn-primary">CALCULATE</button></p>
<p> Unit Rate Per metre cubic </p>
<p> {{rate}}

</div>
</form>
</div>


  `,
  styleUrls: ['./concrete.component.css']
})
export class ConreteBComponent implements OnInit {

cement :number;
aggregrate :number;
sand :number;
formwork :number;
sub_one :number;
waste :number;
sub_two :number;
transport :number;
sub_three :number;
operator :number;
labour :number;
sub_four :number;
cost_labour :number;
hire :number;
cost_person :number;
overhead :number;
profit :number;
rate:number;

  constructor() { }

  ngOnInit() {
  }

BlockForm = new FormGroup ({
  cement : new FormControl (),
  aggregate : new FormControl (),
  sand : new FormControl (),
  formwork : new FormControl (),
  mixer : new FormControl (),
  tool : new FormControl (),
  days : new FormControl (),
  govt : new FormControl (),
  overhead : new FormControl (),
  profit : new FormControl (),
  transport : new FormControl ()
})


solution () {
  
let x = this.BlockForm.value.cement;
let x2 = this.BlockForm.value.aggregate;
let x3 = this.BlockForm.value.sand;
let x4 = this.BlockForm.value.formwork;
let x5 = this.BlockForm.value.mixer;
let x6 = this.BlockForm.value.tool;
let x7 = this.BlockForm.value.days;
let x8 = this.BlockForm.value.govt;
let x9 = this.BlockForm.value.overhead;
let x10 = this.BlockForm.value.profit;
let x11 = this.BlockForm.value.transport;

this.cement = 6 * x
this.aggregrate = 0.84 * x2
this.sand = 0.5 * x3
this.formwork = 0.175 * x4
this.sub_one = +this.cement + +this.aggregrate + +this.sand + +this.formwork
this.waste = 0.05 * +this.sub_one
this.sub_two = +this.sub_one + +this.waste
this.transport = x11 * this.sub_two
this.sub_three = +this.sub_two + +this.transport
this.operator = 1.81 * 2 * (x8 /25) * 0.8
this.labour = 1.81 * 2 * (x8 /25) / 0.8 * 7
this.sub_four = +this.operator + +this.labour
this.cost_labour = +this.sub_four / 2.4
this.hire = x5 /2.5
this.cost_person = (x6 / x7) /2.4
this.sub_four = +this.sub_three + +this.cost_labour + +this.hire + +this.cost_person
this.overhead = x9 * +this.sub_four
this.profit = x10 * +this.sub_four

this.rate = (+this.sub_four + +this.overhead + +this.profit) 
console.log(this.profit)


}


}



