import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'index-entry',
  template: `

<div class="container">
<div class="row box-top">

<div class="col-md-12">
<div class="button-row">
  <button mat-raised-button color="primary"> <a routerLink="/entry/add" routerLinkActive="active">Add Products</a></button>
  <button mat-raised-button color="primary"><a routerLink="/entry/professional" routerLinkActive="active">Add Professional</a></button>  
</div>

</div>
</div>

<div class="row box-bottom">
<div class="col-md-12">
<div class="button-row">
  <button mat-raised-button color="primary"><a routerLink="/entry/rental" routerLinkActive="active">Equipment Rental</a></button>
  <button mat-raised-button color="primary"><a routerLink="/entry/craftman" routerLinkActive="active">Fundis</a></button>  
</div>

</div>
</div>


</div>

  `,
  styleUrls: ['./entry.component.css']
})
export class EntryIndexComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

}



