import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'paint',
  templateUrl: './paint.component.html',
  styleUrls: ['./paint.component.css']
})
export class PaintComponent implements OnInit {

  constructor() { }

 painting :number;
waste :number;
sub_one :number;
transport :number;
sub_two :number;
skilled :number;
unskilled :number;
sub_three :number;
material :number;
sub_four :number;
overhead :number;
profit  :number;
rate : number;


  ngOnInit() {
  }

BlockForm = new FormGroup ({
  paint : new FormControl (),
  govt : new FormControl (),
  tools : new FormControl (),
  days : new FormControl (),
  overhead : new FormControl (),
  profit : new FormControl ()

})

solution () {
 
let x = this.BlockForm.value.paint;
let g = this.BlockForm.value.govt;
let t = this.BlockForm.value.tools;
let d = this.BlockForm.value.days;
let o = this.BlockForm.value.overhead/100;
let p = this.BlockForm.value.profit/100;


 this.painting = 0.35 * x;
this.waste = 0.1 * +this.painting
this.sub_one = +this.painting + +this.waste;
this.transport = 0.1 * +this.sub_one;
this.sub_two = +this.sub_one + +this.transport
this.skilled = ((1.81 * 2 * (g /25))/8) * 0.44
this.unskilled = 0.5 * +this.skilled
this.sub_three = +this.sub_two + +this.skilled + +this.unskilled
this.material = 0.2 * +this.sub_three
this.sub_four = +this.sub_three + +this.material;
this.overhead = o * +this.sub_four
this.profit = p * +this.sub_four
this.rate = (+this.sub_four + +this.overhead + +this.profit) 
console.log (this.rate)

}


}



