import { Component,OnInit }  from '@angular/core';
import { RentalService } from './rental.service';
import { FormGroup,FormControl } from '@angular/forms';
 import { ActivatedRoute,Router } from '@angular/router';

@Component ({
	
	selector:'rentals',
	template:`

<div class="container">
<div class="row">
    <div class="col-md-8">
   <form [formGroup]="RentalForm" (ngSubmit)="onSubmit()">
  
  <select class="form-control" formControlName="rental">
  <option *ngFor="let rental of rentals" [ngValue]="rental.id"> {{rental.company_name}}</option>
  </select>

<div class="form-group">
       <input type="submit" value="Register">
   </div>

  </form>
  </div>
  </div>
</div>


	`,
	styleUrls:['./rental.component.css']

})

export class RentalsComponent implements OnInit {

 constructor(private api : RentalService,private router : Router) { }

  ngOnInit() {

  this.get_rentals ()

  }

  RentalForm = new FormGroup ({
     rental : new FormControl()

  }) 

rentals = [];

get_rentals () {
  return this.api.rentals()
  .subscribe(res => this.rentals = res)
}

  onSubmit () {

let id = this.RentalForm.value;

  this.router.navigate(['/entry/rental/equipment',id.rental]);
 }

}


