import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProfessionalComponent } from './professional.component';
import { NewProfessionalComponent } from './new_professional.component';


const ProfessionalRoutes: Routes = [ 
{path: '', component:ProfessionalComponent,
  children: [
       {path:'',component:NewProfessionalComponent}
  ] }
];

@NgModule ({
	imports:[RouterModule.forChild(ProfessionalRoutes)],
	exports:[RouterModule]
})

export class ProfessionalRouting { }



