import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {  InvoiceService } from './invoice.service';
 
@Component({
  selector: 'one',
  template :`



	<div id="contener">

		<div id="center">
	<div id="side">
			
				
		</div>

		<div id="form">
			<form [formGroup]="FormOne" (ngSubmit)="onSubmit()">
			
			<table width="500" border="0" >
				  <tr>
                <th  scope="col" width="170">BILL TO:	</th>
                <th>
 
             </th>
              </tr>
               <tr>
                <th  scope="col">Creation Setup	</th>
                <td><select id="field">
								<option>individual</option> 
								<option>company</option>
				</select> </td>
              </tr>
              <tr>
                <th scope="row">Company Name </th>
                <td><input type="text" formControlName="name" placeholder="Enter Creation Name"  id="field"  required/><br/></td>
              </tr>
              <tr>
              <th scope="row">Status	</th>
                <td><select  id="field" formControlName="type">
                  <option [ngValue]="8">Manufacturer</option> 
               	  <option [ngValue]="9">Supplier</option>
	                <option [ngValue]="7">Specialist</option>
	                <option [ngValue]="10">Equipment Rental</option>
	                <option [ngValue]="12"> Quality surveying </option>
	                <option [ngValue]="13"> Architect </option>
	                <option [ngValue]="14"> Land scape designer </option>
	                <option [ngValue]="15"> Interior designer </option>
	               <option [ngValue]="16"> Technologists </option>
	               <option [ngValue]="17"> Civil contractors </option>
	               <option [ngValue]="18">  Building contractors</option>
	               <option [ngValue]="19"> Building and civil contractors </option>
	               <option [ngValue]="20">  Mecanical enginear</option>
	               <option [ngValue]="21">  Electrical enginearing</option>

				</select></td>
              </tr>
              <tr>
                <th scope="row">location </th>
                <td> <input type="text" formControlName="address" placeholder="Enter location" required="" id="field"/></td>
              </tr>
              <tr>
                <th scope="row">Region</th>
                <td><input type="text" formControlName="regional" placeholder="Enter region" required="" id="field" /></td>
              </tr>
              <tr>
                <th scope="row">company TIN;</th>
                <td> <input type="integer" formControlName="tin" placeholder="Enter company Tin" required="" id="field"/></td>
              </tr>
              <tr>
                <th scope="row">Contact Person;</th>
                <td>	<input type="text" formControlName="person" placeholder="Enter First Mame" id="jay"> </td>
              </tr>
              <tr>
                <th scope="row">	Email;</th>
                <td>	 <input type="text " formControlName="email" placeholder="Enter Email"  id="field" required/></td>
              </tr>
              <tr>
                <th scope="row">Mobile No ;</th>
                <td>	   <input type="integer" formControlName="phone" placeholder="Enter your Mobile Nom"  id="field" /></td>
              </tr>
              <tr>
                <th scope="row">&nbsp;</th>
                <td><input type="submit" value="NEXT"  id="coments" /></td>
              </tr>
            </table>
			<br/>
			</form>
</div>
		
		</div>


	</div>


  `,
  styleUrls: ['../admin.component.css'],

})

export class FormOneComponent implements OnInit {

 FormOne = new FormGroup ({

  name : new FormControl (),
  type : new FormControl (),
  address : new FormControl(),
  email : new FormControl (),
  person : new FormControl(),
  phone : new FormControl (),
  regional : new FormControl(),
  tin : new FormControl(),
 });

  constructor(private api : InvoiceService,private router : Router) { }

  ngOnInit() {
  
  }

  onSubmit () {
   console.log(this.FormOne.value)
   this.api.store_company(this.FormOne.value)
   .subscribe(res => {
   let id = res;
   this.FormOne.reset()
   if (id > 0) {
   this.router.navigate(['generate',id])
   }
   })
  }
 

}



