import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
 

@Injectable () 

export class RentalService {
	
constructor (private http : Http) { }

private rentalsUlr ="http://127.0.0.1:8000/api/companies";

private equipUrl = "http://127.0.0.1:8000/api/company_equipment";


get_rentals () { 
         return this.http.get(this.rentalsUlr)
        .map ((response:Response) => response.json())
}

filter_rentals (id) { 
     return this.get_rentals()
     .map(rental => rental.filter(res => res.id === +id));
}

get_equipment (id) {
	    return this.http.get(this.equipUrl+'/'+id)
        .map ((response:Response) => response.json())
} 

} 


