import { Component, OnInit } from '@angular/core';
import { RentalService } from './rental.service';
import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'rentals',
  template: `
 <div class="row">

 <div class="col-md-9">
 <h3> Company Profile </h3>
<ul>
  <li *ngFor="let rental of rentals">  <img [src]="'http://localhost:8000/images/'+rental.logo"></li>
</ul>
<ul>
  <li *ngFor="let rental of rentals">  {{rental.profile}}</li>
</ul>
</div>
 <div class="col-md-3">

<table> 
  <tr *ngFor="let rental of rentals">
    <td> {{rental.company_name}}</td>
  </tr>
    <tr *ngFor="let rental of rentals">
    <td> {{rental.contact_person}}</td>
  </tr>
    <tr *ngFor="let rental of rentals">
    <td> {{rental.contact_phone}}</td>
  </tr>
    <tr *ngFor="let rental of rentals">
    <td> {{rental.contact_email}}</td>
  </tr>
</table>

 </div> 
</div>
  `,
  styleUrls: ['./rental.component.css']
})

export class RentalsComponent implements OnInit {

  constructor(private api : RentalService,private route : ActivatedRoute) { }

   id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {
        this.route.data
      .subscribe ((data => {
         this.rentals=data.res
       console.log(data.res);
         }));
  }
 
 rentals = [];

}



