import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ProductService } from '../add_product.service';
import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'agent',
  template: `
  <div class="container">
<div class="row">
    <div class="col-md-8">
   <form [formGroup]="AgentForm" (ngSubmit)="onSubmit()">
  
  <select class="form-control" formControlName="agent">
  <option *ngFor="let agent of agent" [ngValue]="agent.id">{{agent.company_name}}</option>
  </select>

<div class="form-group">
       <input type="submit" value="Register">
   </div>

  </form>
  </div>
  </div>
</div>
  `,
  styleUrls: ['../product.component.css']
})
export class ExistAgentComponent implements OnInit {

  constructor(private api : ProductService,private router : Router) { }

  ngOnInit() {
 this.get_agent ()
  }

  agent = [];

  AgentForm = new FormGroup ({
  agent : new FormControl()
  })

get_agent () { 
  this.api.agencies()
  .subscribe(res => {this.agent = res }) 
}

onSubmit () {

 const id = this.AgentForm.value;
  this.router.navigate(['/agent-form/agent-product',id.agent]);
}

}
  


