import { Component, OnInit } from '@angular/core';
 import { FormGroup,FormControl } from '@angular/forms';
  import { ActivatedRoute,Router } from '@angular/router';
import { ProfessionalService } from './professional.service';

@Component({
  selector: 'new-pro',
  template: `
<div class="container">
 <div class="row">
    <div class="col-md-8">
    
   
    <form [formGroup]="ProfessionalForm" (ngSubmit)="onSubmit()"> 
  
      <div class="form-group">
    <label>Fundi's</label>
    <input type="text" class="form-control" placeholder="User Name" formControlName="name">
    </div>
  


  <label> Experience </label>
   <select class="form-control" formControlName="expert">
  <option [ngValue]="expert_1"> Masonry </option>
  <option [ngValue]="expert_2"> Electricial </option>
  <option [ngValue]="expert_3"> Plumber </option>
  <option [ngValue]="expert_4"> Steel Fixer </option>
  <option [ngValue]="expert_5"> Painter </option>
  <option [ngValue]="expert_6"> ICT </option>
  <option [ngValue]="expert_7"> Interior </option>
  <option [ngValue]="expert_8"> Silks </option>
  </select>

    <div class="form-group">
    <label>Regional </label>
    <input type="text" class="form-control" placeholder="Regional" formControlName="regional">
    </div>


    <div class="form-group">
    <label>District </label>
    <input type="text" class="form-control" placeholder="District" formControlName="district">
    </div>

    <div class="form-group">
    <label>Phone</label>
    <input type="email" class="form-control" placeholder="Phone" formControlName="phone">
    </div>


 <div class="form-group">
       <input type="submit" value="Register">
      </div>


</form>

<div *ngIf="check" class="show">

<table class="table">
<tr>
 <td>Name</td>  <td class="ans"> {{fundi.name}} </td>
</tr>
<tr>
 <td> Experience  </td> <td class="ans"> {{fundi.expert}} </td>
</tr>
<tr>
 <td> Regional </td>  <td class="ans"> {{fundi.regional}} </td>
</tr>
<tr>
 <td> District  </td>  <td class="ans">{{fundi.district}} </td>
</tr>
<tr>
 <td> Phone  </td>  <td class="ans"> {{fundi.phone}}</td>
</tr>
</table>

succesfully Registered!!!
</div>

   </div>
  </div>
</div>

  `,
  styleUrls: ['./professional.component.css']
})

export class NewProfessionalComponent implements OnInit {

constructor( private api : ProfessionalService,private router:Router) { } 

expert_1='Masonry';
expert_2='Electricial';
expert_3='Plumber';
expert_4='Steel Fixer';
expert_5='Painter';
expert_6='ICT';
expert_7='Interior';
expert_8='Silks';

fundi = [];
check = null;

ngOnInit() {    }

ProfessionalForm = new FormGroup ({
  name : new FormControl (),
  expert : new FormControl (),
  regional : new FormControl (),
  district : new FormControl(),
  phone : new FormControl (),
  })
 
onSubmit () {
     console.log(this.ProfessionalForm.value)

this.api.new_fundi (this.ProfessionalForm.value)
.subscribe(res =>{ this.fundi = res; this.check = 'true'; this.ProfessionalForm.reset()})

}

}



