import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { EntryComponent } from './entry.component';
import { EntryIndexComponent } from './entry_index.component';

const EntryRoutes: Routes = [ 
{path: '', component:EntryComponent,
  children: [
    {path:'',component:EntryIndexComponent},

     {
    path: 'add',
    loadChildren: 'app/entry/product/product.module#ProductModule',
    data: { preload: true }
 },
 {
    path: 'professional',
    loadChildren: 'app/entry/professional/professional.module#ProfessionalModule',
    data: { preload: true }
 },

 {
    path: 'rental',
    loadChildren: 'app/entry/rental/rental.module#RentalModule',
    data: { preload: true }
 },
  { 
    path: 'craftman',
    loadChildren: 'app/entry/craftman/craftman.module#CraftmanModule',
    data: { preload: true } 
 }

  ] }
];

@NgModule ({
	imports:[RouterModule.forChild(EntryRoutes)],
	exports:[RouterModule]
})

export class EntryRouting { }



