import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProductComponent } from './product.component';
import { ProductIndexComponent } from './product_index.component';
import { ProductAgentComponent } from './agents/product_agent.component';
import { ProductManufacturerComponent } from './manufacturer/product_manufacturer.component';
import {  ProductSupplierComponent } from './suppliers/product_supplier.component';

import { NewAgentComponent } from './agents/new_agent.component';
import { ExistAgentComponent } from './agents/exist_agent.component';
import { NewSupplierComponent } from './suppliers/new_supplier.component';
import { ExistSupplierComponent } from './suppliers/exist_supplier.component';
import { NewManufacturerComponent } from './manufacturer/new_manufacturer.component';
import { ExistManufacturerComponent } from './manufacturer/exist_manufacturer.component';
import { ManufacturerLogoComponent }  from './manufacturer/manufacturer_logo.component';

import { LogoComponent } from './agents/logo_form.component';

import { SupplierLogoComponent } from './suppliers/supplier_logo.component';

import { AgentsComponent } from './agents/agent.component';
import { ManufacturersComponent } from './manufacturer/manufacturer.component';
import { SuppliersComponent } from './suppliers/supplier.component';

const ProductRoutes: Routes = [ 
 {path:'' ,component:ProductComponent,
    children: [
     {path:'', component:ProductIndexComponent,
       children: [

          {path:'agent-form', component:AgentsComponent,
            children: [
             {path:'new', component:NewAgentComponent},
             {path:'exist', component:ExistAgentComponent},
             {path: 'logo/:id', component:LogoComponent},
             {path: 'agent-product/:id', component:ProductAgentComponent}
            ]},
          
          {path:'supplier-form', component:SuppliersComponent,
             children: [ 
              {path:'new', component:NewSupplierComponent},
              {path:'exist', component:ExistSupplierComponent},
              {path:'supplier_logo/:id', component:SupplierLogoComponent},
              {path: 'supplier-product/:id', component:ProductSupplierComponent}
            ]},

          {path:'manu-form', component:ManufacturersComponent,
            children:[
             {path:'new', component:NewManufacturerComponent},
             {path:'exist', component:ExistManufacturerComponent},
             {path:'manu_logo/:id', component:ManufacturerLogoComponent},
             {path: 'manu-product/:id', component:ProductManufacturerComponent}

          ]}
       ]}
    ]
 }
];

@NgModule ({
	imports:[RouterModule.forChild(ProductRoutes)],
	exports:[RouterModule]
})

export class ProductRouting { }
 


