import { NgModule} from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ViewComponent } from './view.component';

import { HomeComponent } from './home/home.component';

import { ProductComponent } from './products/product.component';
import { ProductSeriesComponent } from './products/product_serie.component';
import {  IndexComponent } from './products/index.component';
import { ProductCompanyComponent } from './products/company.component';

import { IndexSupplierComponent } from './suppliers/index_supplier.component';
import { SuppliersComponent } from './suppliers/suppliers.component';
import { SupplierComponent } from './suppliers/supplier.component';

import { RentalsComponent } from './rentals/rentals.component';
import { RentalComponent } from './rentals/rental.component';
import { IndexRentalComponent } from './rentals/index_rental.component';
import { EquipmentsComponent } from './rentals/equipments.component';

import { ProfessionalComponent } from './professionals/professional.component';
import { ProfessionlsComponent } from './professionals/professionals.component';
import { ProfessionalTypesComponent } from './professionals/professional_type.component';
import { GalleryComponent } from './professionals/gallery.component';
import { IndexProfessionlComponent } from './professionals/index_professional.component'

import { IndexManufacturerComponent } from './manufacturers/index_manufacturer.component';
import { ManufacturerComponent } from './manufacturers/manufacturer.component';
import { ManufacturersComponent }  from './manufacturers/manufacturers.component';

import { TermsComponent } from './footer/terms.component';
import { PrivacyComponent } from './footer/privacy.component';
import { FormComponent } from './home/form.component';

import { ProductResolver } from './products/product.resolver';
import { SubProductResolver } from './products/sub_product.resolver';
import { ManufacturerResolver } from './manufacturers/manufacturer.resolver';
import { SupplierResolver } from './suppliers/supplier.resolver';
import { SubProfessionalResolver }  from './professionals/sub_professional.resolver';
import { ProfessionalResolver } from './professionals/professional.resolver';
import { RentalResolver } from './rentals/rental.resolver';

const ViewRoutes: Routes = [ 
 
{path: '', component:ViewComponent,
   children: [
     
      { path:'', component:HomeComponent},
      {path:'term', component:TermsComponent},
      {path:'privacy', component:PrivacyComponent}, 
      {path:'form_join', component:FormComponent},
       {
        path: 'admins',
        loadChildren: 'app/view/admin/admin.module#AdminModule',
        data: { preload: true }
     },

      { 
        path:'buliding-rates',
        loadChildren: 'app/view/formular/formula.module#FormularModule',
        data: { preload: true }

      }, 
      {path:'products/:id',component:ProductComponent,resolve:{ res: ProductResolver},},
      {path:'company/:name', component:ProductCompanyComponent},
      {path:'sub_product/:name', component:ProductSeriesComponent,resolve:{ res: SubProductResolver},},
    
     {path:'supplier/:id', component:SupplierComponent,
         children: [
          {path:'',component:IndexSupplierComponent,resolve:{ res: SupplierResolver},
             children: [
            {path:':id', component:SuppliersComponent}
             ]
           }
         ]
      },
       {path:'manufacturer/:id', component:ManufacturerComponent,
         children: [
          {path:'',component:IndexManufacturerComponent,resolve:{ res: ManufacturerResolver},
             children: [
            {path:':id', component:ManufacturersComponent}
             ]
           }
         ]
      },
      
       {path:'type_pros/:name',component:ProfessionalTypesComponent,resolve:{ res: ProfessionalResolver},},
       {path:'sub_pro/:name', component:ProfessionlsComponent,resolve:{ res: SubProfessionalResolver},},
       {path:'pro/:id', component:IndexProfessionlComponent,
              children:
              [{path:'gallery/:id',component:GalleryComponent}]
          },
      {
         path:'rental/:id', component:RentalComponent,
            children: [
              {path:'',component:IndexRentalComponent,
             children: [
              {path:'', component:RentalsComponent,resolve:{ res: RentalResolver},},
              {path:'equipment/:id',component:EquipmentsComponent}
             ]
           }
         ]
      }

    ]
  }];

@NgModule ({ 
	imports:[RouterModule.forChild( ViewRoutes ) ],
	exports:[RouterModule],
	providers: [ProductResolver,SubProductResolver,ManufacturerResolver,SupplierResolver,SubProfessionalResolver,ProfessionalResolver,RentalResolver],
  
}) 

export class ViewRouting { } 


