import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable () 

export class InvoiceService {

constructor (private http : Http) { }

private itemUrl = "http://127.0.0.1:8000/api/items";
private new_companyUrl =  "http://127.0.0.1:8000/api/store-company";
private new_itemUrl = "http://127.0.0.1:8000/api/store_invoice";
private invoiceUrl = "http://127.0.0.1:8000/api/company_invoice";
private companyUrl = "http://127.0.0.1:8000/api/company";

get_items () {
	return this.http.get(this.itemUrl)
	.map((response : Response) => response.json())
}

store_company (data) {
    let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_companyUrl,JSON.stringify(data),options)
	.map((response:Response) => response.json());
}

add_item (data,id) {
	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_itemUrl+'/'+id,JSON.stringify(data),options)
	.map((response:Response) => response.json());
}

get_invoices (id) {
	return this.http.get(this.invoiceUrl+'/'+id)
	.map((response:Response) => response.json());
}

get_company (id) { 
  return this.http.get(this.companyUrl+'/'+id)
  .map((response : Response) => response.json());
}


}


