import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { CraftmanService } from './craftman.service';
import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'logo',
  template: `


     <p> Please Upload Company Logo </p><br>

    <input type="file"   (change)="select($event)"><br>
    <button (click)="upload()"> upload </button>




  `,
  styleUrls: ['./craftman.component.css']
})
export class LogoComponent implements OnInit {

 logo : File = null;
 type = [];
  constructor(private api : CraftmanService, private route : ActivatedRoute, private router : Router) { }

  ngOnInit() {
  }

  id = this.route.snapshot.paramMap.get('id');

 select (event) {
 this.logo = event.target.files[0];
 }

  upload () {

   console.log(this.logo)
  let formData : FormData = new FormData(); 

  formData.append('Logo', this.logo, this.logo.name);  
   this.api.upload(this.id,formData)
     .subscribe(res => { console.log(res)
if (this.id) {

this.api.types (this.id)
.subscribe (res => {  this.type = res 

 console.log(this.type)

switch (this.type[0].type_name) { 

   case "Professional": 
   this.router.navigate(['professionalin',this.id])
      break; 

   case "Rental": 
 this.router.navigate(['rental_equipment',this.id])
      break; 

   default: 
 this.router.navigate(['product',this.id])
      break;       

}})


 } else { console.log('no access')
       

     }

     })

  }




}



