import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import { ProfessionalService } from './professional.service';


@Component({
  selector: 'professionals',
  template: `
   <div class="container">
<div class="row">
<div class="col-md-12">
<div class="row">
<div class="col-md-12">
<ol class="breadcrumb">
  <li><a href="#">Home</a></li>
  <li><a href="#"> Professional Type</a></li>
<li><a href="#">--</a></li>
</ol>

</div>
</div>
  <ul>
    <li *ngFor="let sub of subs"> {{sub.id}} </li>
  </ul>
</div>
</div>
</div>
  `,
  styleUrls: ['./professional.component.css']
})

export class ProfessionlsComponent implements OnInit {

  name = this.route.snapshot.paramMap.get('name'); 

  constructor(private api : ProfessionalService,private route : ActivatedRoute) { }

  ngOnInit() {

        this.route.data
      .subscribe ((data => {
         this.subs = data.res
       console.log(data.res);
         }));
  }
 
 subs = [];
}



