import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ProductService } from '../add_product.service';
 import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'product',
  template: `
<div class="container">
<div class="row">
    <div class="col-md-8">
    
    
<form [formGroup]="ProductForm" (ngSubmit)="onSubmit()">
  
 <select class="form-control" (change)="select($event)">
  <option *ngFor="let category of category" [ngValue]="category.id">{{category.category_name}}</option>
  </select>

   <select class="form-control" formControlName="type">
  <option *ngFor="let sub of subs" [ngValue]="sub.id">{{sub.product_name}}</option>
  </select>

    <div class="form-group">
    <label>Product Code</label>
    <input type="text" class="form-control" placeholder="Product Code Number" formControlName="name">
  </div>
    <div class="form-group">
    <label>Size</label>
    <input type="text" class="form-control" placeholder="Size" formControlName="size">
  </div>
    <div class="form-group">
    <label>Price</label>
    <input type="integer" class="form-control" placeholder="Price" formControlName="price">
  </div>

   <div class="form-group">
    <label>Units</label>
    <input type="text" class="form-control" placeholder="Units" formControlName="unit">
  </div>
 
    <div class="form-group">
    <label>quantity</label>
    <input type="text" class="form-control" placeholder="quantity" formControlName="quantity">
  </div>


<div class="form-group">
       <input type="submit" value="Register">
      </div>


</form>

   </div>
  </div>
</div>

  `,
  styleUrls: ['../product.component.css']
})
export class ProductAgentComponent implements OnInit {

constructor(private api : ProductService, private route : ActivatedRoute) { }

id = this.route.snapshot.paramMap.get('id');

ProductForm = new FormGroup ({
  name : new FormControl(),
  size : new FormControl(),
  price : new FormControl (),
  unit : new FormControl(),
  quantity : new FormControl(),
  type : new FormControl()

})

  ngOnInit() {
 this.getCategorys () 
   
  }

category = [];
chosen_category:any = null;
subs = [];

  getCategorys () { 
    this.api.getCategory()
    .subscribe(res => { this.category = res}) 
    }

get_sub () { 
   this.api.getSubCategory(this.chosen_category)
   .subscribe(res => {this.subs = res}) }

onSubmit () { 
  console.log(this.ProductForm.value)
  this.api.new_product(this.ProductForm.value)
  .subscribe (res => { 
   const id = res;
   const agent_id = this.route.snapshot.paramMap.get('id');
   console.log(agent_id,id)
   let detail = {aid:agent_id,pid:id};
   this.api.agent_product(detail)
   .subscribe(res => {})

  })
}

 select (event) {
  this.chosen_category = event.target.value;
  console.log(this.chosen_category)
  this.get_sub ()
 }

}



