import { Component, OnInit } from '@angular/core';
import { ManufacturerService } from './manufacturer.service';
import { ActivatedRoute,Router } from '@angular/router';


@Component({
  selector: 'index',
  template: `
<div class="container">
<div class="row intro">
<div class="col-md-12">
 

<div class="row">
<div class="col-md-12">

<ol class="breadcrumb">
  <li><a href="#">Home</a></li>
  <li><a href="#"> Manufacturer</a></li>
<li *ngFor="let company of company">{{company.company_name}}</li>
</ol>

</div>
</div>

<table>

<tr *ngFor="let company of company">
<td> <img [src]="'http://localhost:8000/images/'+company.logo"></td>
</tr>
</table>

<div class="row">

<div class="col-md-10">

<ul class="nav nav-pills">
  <li role="presentation" class="active"><a href="#">Company Profile</a></li>
  <li role="presentation"><a href="#">Products</a></li>
  <li role="presentation"><a href="#">News </a></li>
</ul>

<table>

<tr *ngFor="let company of company">
<td> {{company.company_name}}</td>
</tr>
<tr *ngFor="let company of company">
<td id="profile"> {{company.profile}} </td>
</tr>

</table>
</div>
<div class="col-md-2">
<table class="table">

 <tr *ngFor="let company of company">
 <td> {{company.address}}</td>
 </tr>
 <tr *ngFor="let company of company">
 <td> {{company.contact_person}}</td> 
 </tr>
   <tr *ngFor="let company of company">
 <td>{{company.regional}} </td>
 </tr>
  <tr *ngFor="let company of company">
 <td>{{company.district}} </td>
 </tr>
  <tr *ngFor="let company of company">
 <td> {{company.contact_email}}</td>
 </tr>
 <tr *ngFor="let company of company">
 <td> {{company.contact_phone}}</td>
</tr>
</table>
</div>
</div>
</div>
</div>
</div>
  `, 
  styleUrls: ['./manufacturer.component.css']
})

export class IndexManufacturerComponent implements OnInit {

  constructor( private api : ManufacturerService, private route : ActivatedRoute) { } 
 
 id = this.route.snapshot.paramMap.get('id');
 company = [];


  ngOnInit() {
        this.route.data
      .subscribe ((data => {
         this.company=data.res
       console.log(data.res);
         }));

  }



}



