import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable ()  
 
export class ProfessionalService {

 constructor (private http : Http) { }

 private fundiUrl = "http://127.0.0.1:8000/api/fundi";
 private new_fundiUrl ="http://127.0.0.1:8000/api/fundi-store";
 private galleryUrl ="http://127.0.0.1:8000/api/upload";
  private LogoUrl ="http://127.0.0.1:8000/api/upload-professional";


fundi () {
return this.http.get(this.fundiUrl)
 .map((response:Response) => response.json());
}

new_fundi (fundi) {
	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
 return this.http.post(this.new_fundiUrl,JSON.stringify(fundi),options)
 .map((response:Response) => response.json());
 }

}


