import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
@Component({
  selector: 'clearance',
   template: `


<div class="row">
<div class="col-md-11 main">

<h4> Unit Rate Formula </h4>

</div>
</div>

<div class="row">
<form [formGroup]="ClearForm" class="form-horizontal" (ngSubmit)="solution()">  
<div class="col-md-7 insert">


   <div class="form-group">
    <label  class="col-sm-6 control-label">Govrt Basic Salary</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="govt">
    </div>
   </div>

     <div class="form-group">
    <label class="col-sm-6 control-label">Cost of Tools</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="tools">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Persnaldays required </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="days">
    </div>
   </div>
   
     <div class="form-group">
    <label  class="col-sm-6 control-label"> Overhead Expenses</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="overhead">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Profit </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="profit">
    </div>
   </div>
   

</div>	

<div class="col-md-5 answer">

<p>  <button type="submit" class="btn btn-primary">CALCULATE</button></p>
<p> Unit Rate Per metre square </p>
<p> {{rate}}

</div>
</form>
</div>





   `,
  styleUrls: ['./earth.component.css']
})

export class ClearanceComponent implements OnInit {



  constructor() { }

ClearForm = new FormGroup ({
	govt : new FormControl (),
	tools : new FormControl (),
	days : new FormControl (),
	overhead : new FormControl (),
	profit : new FormControl ()

})

  ngOnInit() {
  } 



labour:number;
cost:number;
replacement:number;
sub_one:number;
tools:number;
days :number;
incidentals:number;
mandays:number;
cost_tools:number;
sub_two:number;
overhead:number;
profit:number;
rate:number;

solution () {
 
let g = this.ClearForm.value.govt;
let t = this.ClearForm.value.tools;
let d = this.ClearForm.value.days;
let o = this.ClearForm.value.overhead/100;
let p = this.ClearForm.value.profit/100;



this.labour = (1.81 * g/25) * 0.33333333;
this.cost  = t;
this.replacement = this.cost * 0.1;
this.sub_one = +this.cost + +this.replacement;
this.tools = 0.5 * this.sub_one;
this.days = d;
this.incidentals = 0.15 * this.days;
this.mandays = +this.days + +this.incidentals;
this.cost_tools = (this.tools / this.mandays ) * 0.33333333;
this.sub_two  = +this.labour + +this.cost_tools;
this.overhead = o * this.sub_two;
this.profit = p * this.sub_two;

this.rate = (+this.sub_two + +this.overhead + +this.profit) * 0.15
console.log (this.rate)
}

}



