import { NgModule } from '@angular/core';
import { EntryRouting } from './entry.routing';
import { ProductModule } from './product/product.module';
import { ProfessionalModule } from './professional/professional.module';
import { RentalModule } from './rental/rental.module';
import { CraftmanModule } from './craftman/craftman.module';
import { EntryComponent } from './entry.component';
import { EntryIndexComponent } from './entry_index.component';
import {MatButtonModule} from '@angular/material/button';

@NgModule ({
	
	declarations: [EntryComponent,EntryIndexComponent],
	imports:[EntryRouting,MatButtonModule,ProductModule,ProfessionalModule,RentalModule,CraftmanModule],
	exports:[]
})

export class EntryModule { } 


