import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ProductService } from '../add_product.service';
import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'exist-supplier',
  template: `

<div class="container">
<div class="row">
    <div class="col-md-8">
   <form [formGroup]="SupplierForm" (ngSubmit)="onSubmit()">
  
  <select class="form-control" formControlName="supplier">
  <option *ngFor="let supplier of supplier" [ngValue]="supplier.id">{{supplier.company_name}}</option>
  </select>

<div class="form-group">
       <input type="submit" value="Register">
   </div>

  </form>
  </div>
  </div>
</div>

  `,
 styleUrls: ['../product.component.css']
})
export class ExistSupplierComponent implements OnInit {

  constructor(private api : ProductService,private router : Router) { }

  ngOnInit() {
 this.get_supplier ()
  }

  supplier = [];

  SupplierForm = new FormGroup ({
  supplier : new FormControl()
  })

get_supplier () { 
  this.api.suppliers ()
  .subscribe(res => {this.supplier = res }) 
}

onSubmit () {

 const id = this.SupplierForm.value;
  this.router.navigate(['/supplier-form/supplier-product',id.supplier]);
}

}



