import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormularComponent } from './formula.component';
import { BlockworkComponent } from './blockwork/block.component';
import { ConreteComponent } from './concrete/concrete.component';
import { FoundationComponent } from './foundation/foundation.component';
import { HardcoreComponent } from './foundation/hardcore.component';
import { SoilComponent } from './foundation/soil.component';
import { PaintComponent } from './paint/paint.component';
import { PlasterComponent } from './plaster/plaster.component';
import { ReinforceComponent } from './reinforce/reinforce.component';
import { TileComponent }  from './tile/tile.component';
import { SmallBlockComponent } from './blockwork/small.component';
import { LargeBlockComponent } from './blockwork/large.component';

import { PlasteringComponent } from './plaster/plastering.component';
import { ScreenComponent } from './plaster/screen.component';

import { EarthWorkComponent } from './earthwork/earth.component';
import { SiteEvcavationComponent } from './earthwork/site_evcavation.component';
import { ClearanceComponent } from './earthwork/clearance.component';
import { EvcavationComponent } from './earthwork/evcavation.component';
import { BackfillComponent } from './earthwork/backfill.component';
import { CartAwayComponent }  from './earthwork/cart_away.component';
import { FillComponent } from './earthwork/selected-fills.component';

import { ConreteAComponent } from './concrete/concreteA.component';
import { ConreteBComponent } from './concrete/concreteB.component';

const FormulaRoutes : Routes = [
  {path:'' , component: FormularComponent,
    children: [
      {path:'block', component:BlockworkComponent,
        children : [
            {path:'small',component:SmallBlockComponent },
            {path:'large',component:LargeBlockComponent }
        ]
      },
      {path:'plaster', component:PlasterComponent,
        children: [
         {path:'plastering', component:PlasteringComponent},
         {path:'screen', component:ScreenComponent}
        ]
      },
      {path:'reinforce', component:ReinforceComponent},
      {path:'tile', component:TileComponent},
      {path:'concrete', component:ConreteComponent,
        children: [
         {path: 'A', component:ConreteAComponent},
         {path: 'B', component:ConreteBComponent}

        ]

      },
      {path:'foundation', component:FoundationComponent,
        children: [
          {path:'hardcore', component:HardcoreComponent},
          {path:'soil', component:SoilComponent} ]},
      {path:'paint', component:PaintComponent},
      {path: 'earthwork' , component:EarthWorkComponent,
        children: [
          {path:'site', component:SiteEvcavationComponent},
          {path: 'clearance', component:ClearanceComponent },
          {path: 'evcavation' , component:EvcavationComponent },
          {path: 'backfill' , component:BackfillComponent },
          {path: 'cart' , component:CartAwayComponent },
          {path: 'fill' , component:FillComponent },

         ]
      }

      ]
    }
];


@NgModule ({
	
	imports:[ RouterModule.forChild(FormulaRoutes)],
	exports:[RouterModule]
})

export class FormulaRouting {}


