import { Component, OnInit } from '@angular/core';
import { ProductService } from './product.service';
import { ActivatedRoute,Router } from '@angular/router';


@Component({
  selector: 'company',
  template: `
<div class="container">
<div class="row intro">
<div class="col-md-12">


<div class="row">
<div class="col-md-12">
<ol class="breadcrumb">
  <li><a href="#">Home</a></li>
  <li><a href="#"> Manufacturer</a></li>

</ol>

</div>
</div>



<div class="row">

<div class="col-md-10">


<table>

<tr *ngFor="let company of company">
<td><img [src]="'http://localhost:8000/images/'+company.logo"> </td><td> <h3>{{company.company_name}} </h3></td>
</tr>
</table>

<ul class="nav nav-pills">
  <li role="presentation" class="active"><a href="#">Company Profile</a></li>
  <li role="presentation"><a href="#">Products</a></li>
  <li role="presentation"><a href="#">News </a></li>
</ul>

<table>

<tr *ngFor="let company of company">
<td id="profile"> {{company.profile}} </td>
</tr>

</table>
</div>
<div class="col-md-2">
<table class="table">

 <tr *ngFor="let company of company">
 <td> {{company.address}}</td>
 </tr>
 <tr *ngFor="let company of company">
 <td> {{company.contact_person}}</td> 
 </tr>
   <tr *ngFor="let company of company">
 <td>{{company.regional}} </td>
 </tr>
  <tr *ngFor="let company of company">
 <td>{{company.district}} </td>
 </tr>
  <tr *ngFor="let company of company">
 <td> {{company.contact_email}}</td>
 </tr>
 <tr *ngFor="let company of company">
 <td> {{company.contact_phone}}</td>
</tr>
</table>
</div>
</div>
</div>
</div>
</div>
  `,
  styleUrls: ['./product.component.css'],

})

export class ProductCompanyComponent implements OnInit {

  constructor(private api : ProductService,private route : ActivatedRoute) { }

 name = this.route.snapshot.paramMap.get('name');

  ngOnInit() {
    this.api.get_company(this.name)
    .subscribe(res => { this.company = res})
  }
 company = [];
  
}



