import { Component, OnInit } from '@angular/core';
import { RentalService } from './rental.service';
import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'equipment',
  template: `

<div class="row">

 <div class="col-md-9">
 <h3> Company Rent Equipments </h3>

<table>
<tr *ngFor="let equipment of equipments">
  <td> Equipment Name </td> <td> {{equipment.equipment_name}} </td>
</tr>
<tr *ngFor="let equipment of equipments">
  <td> Specifications </td>  <td> {{equipment.specification}} </td>
</tr>
<tr *ngFor="let equipment of equipments">
  <td> Monthly charges </td> <td> {{equipment.month_charge}} </td>
</tr>
<tr *ngFor="let equipment of equipments">
  <td> Weekly charges  </td> <td> {{equipment.week_charge}} </td>
</tr>
<tr *ngFor="let equipment of equipments">
  <td> Daily charges </td>  <td> {{equipment.day_charge}} </td>
</tr>
<tr *ngFor="let equipment of equipments">
  <td> Equipment Code </td> <td> {{equipment.equipment_code}} </td>
</tr>

</table>


 </div>

 <div class="col-md-3">

 </div>







  `,
  styleUrls: ['./rental.component.css']
})

export class EquipmentsComponent implements OnInit {

 equipments = [];

  constructor(private api : RentalService,private route : ActivatedRoute) { }

 id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {
    this.equip()
  }
 
 equip () {
   this.api.get_equipment(this.id)
   .subscribe( res => {this.equipments = res})
 }


}



