import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

 
@Injectable ()

export class ProductService { 

constructor (private http : Http) { }

private product_categoryUrl = "http://127.0.0.1:8000/api/product-category";
private productUrl = "http://127.0.0.1:8000/api/product-detail"; 
private companyUrl = "http://127.0.0.1:8000/api/company_name";




get_products (id) { 
	return this.http.get(this.product_categoryUrl+'/'+id)
   .map((response : Response) => response.json());
}

get_product (name) { 
   return this.http.get(this.productUrl+'/'+name)
   .map((response : Response) => response.json());

}


 filter_category (id) { 
  return this.get_products(id)
   .map(cate => cate.filter(res => res.category_id === +id));
 }

 filter_sub (name) { 
  return this.get_product (name)
   .map(sub => sub.filter(res => res.product_name === name));
 }


get_company (name) {
	return this.http.get(this.companyUrl+'/'+name)
   .map((response : Response) => response.json());
} 


}




