import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import { ProfessionalService } from './professional.service';

@Component({
  selector: 'types',
  template: `

<div class="container">
<div class="row intro">
<div class="col-md-12">
 

<div class="row">
<div class="col-md-12">
<ol class="breadcrumb">
  <li><a href="#">Home</a></li>
  <li><a href="#"> Professional Type</a></li>

</ol>

</div>
</div>

<div class="row">
<div class="col-md-9">

<div class="row">
<div class="col-md-12 header">
 <ul>
    <li *ngFor="let type of types"><h1> {{type.company_name}} </h1></li>
 </ul>
 </div>
</div>

</div>


<div class="col-md-3">

<ul>
    <li *ngFor="let type of types"> {{type.id}} </li>
 </ul>
 <ul>
    <li *ngFor="let type of types"> {{type.contact_person}} </li>
 </ul>
 <ul>
    <li *ngFor="let type of types"> {{type.contact_email}} </li>
 </ul>
 <ul>
    <li *ngFor="let type of types"> {{type.address}} </li>
 </ul>
<ul>
    <li *ngFor="let type of types"> {{type.contact_phone}} </li>
 </ul>
 <ul>
    <li *ngFor="let type of types"> {{type.regional}} </li>
 </ul>
 <ul>
    <li *ngFor="let type of types"> {{type.district}} </li>
 </ul>
</div>
</div>


 </div>
 </div>
 </div>

  `,
  styleUrls: ['./professional.component.css']
})

export class ProfessionalTypesComponent implements OnInit {

  constructor(private api : ProfessionalService,private route : ActivatedRoute) { }

 name = this.route.snapshot.paramMap.get('name');

  ngOnInit() {
      this.route.data
      .subscribe ((data => {
         this.types = data.res
       console.log(data.res);
         }));
  }

  types = [];
 

}



