import { Component, OnInit, ViewChild,ElementRef } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {  InvoiceService } from './invoice.service';
import { NgModel } from '@angular/forms';
import * as jsPDF from 'jspdf';


@Component({
  selector: 'two',
  template: `


<div class="col-md-4">


</div>

<div class="col-md-8">


     <h5> company </h5>


 <form class="form-horizontal" [formGroup]="FormOne" (ngSubmit)="oneSubmit()">

   
 
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">Choose planning</label>
     <div class="col-sm-10">
      <select formControlName="item">
       <option *ngFor="let service of service" [ngValue]="service.id">{{service.invoice_name}} </option>
     </select>
  </div>
  </div>
 

 <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
      <button type="submit" class="btn btn-default">ADD</button>
    </div>
  </div>
</form>


 <form class="form-horizontal" [formGroup]="FormTwo" (ngSubmit)="twoSubmit()">
 <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
      <div class="checkbox">
        <label>
           <input type="radio" id="optionsRadios2" value='3' formControlName="month"> 1 months
        </label>
      </div>
    </div>
  </div>
  <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
      <div class="checkbox">
        <label>
           <input type="radio"  id="optionsRadios2" value='2' formControlName="month">3 months
        </label>
      </div>
    </div>
  </div>
  <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
      <div class="checkbox">
        <label>
           <input type="radio"  id="optionsRadios2" value="1" formControlName="month"> 6 months
        </label>
      </div>
    </div>
  </div>
  <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
      <div class="radio">
        <label>
          <input type="radio"  id="optionsRadios2" formControlName="month"> 12 months
        </label>
      </div>
    </div>
  </div>
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">From</label>
     <div class="col-sm-10">
      <input type="text" class="form-control" id="inputEmail3" placeholder="From" formControlName="start">
  </div>
  </div>
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">TO</label>
     <div class="col-sm-10">
      <input type="text" class="form-control" id="inputEmail3" placeholder="To" formControlName="end">
  </div>
  </div>
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">Amount </label>
     <div class="col-sm-10">
      <input type="text" class="form-control" id="inputEmail3" placeholder="Amount to Display here" formControlName="display">
  </div>
  </div>
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">Total</label>
     <div class="col-sm-10">
      <input type="text" class="form-control" id="inputEmail3" placeholder="Total" formControlName="total">
  </div>
  </div>
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">VAT</label>
     <div class="col-sm-10">
      <input type="text" class="form-control" id="inputEmail3" placeholder="VAT" formControlName="vat">
  </div>
  </div>
   <div class="form-group">
     <label for="inputEmail3" class="col-sm-2 control-label">Discount</label>
     <div class="col-sm-10">
      <input type="text" class="form-control" id="inputEmail3" placeholder="Discount" formControlName="discount">
  </div>
  </div>

    <div class="form-group">
    <div class="col-sm-offset-2 col-sm-10">
      <button type="submit" class="btn btn-default">Generate</button>
    </div>
  </div>
  </form>

</div>




  `,
  styleUrls: ['../admin.component.css'],

})

export class FormTwoComponent implements OnInit {

  items = [];
  service = [];
  total :any;

  constructor( private api : InvoiceService,private router : Router, private route : ActivatedRoute) { }

 id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {
  this.get_items ()
  }

get_items () {
  this.api.get_items().
  subscribe(res => {this.service = res}) 
}

FormOne = new FormGroup ({
    item : new FormControl (),
});

  FormTwo = new FormGroup ({
    month : new FormControl (),
  start : new FormControl (),
  end  : new FormControl (),
  amount: new FormControl (),
  total : new FormControl (),
  display : new FormControl (),
  vat : new FormControl (),
  discount : new FormControl ()
 })



oneSubmit () {
 this.api.add_item(this.FormOne.value,this.id)
 .subscribe(res => {console.log ('yes')});

  console.log(this.FormOne.value,this.id)
}

twoSubmit () {
  console.log(this.FormTwo.value)
}


}



