import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable ()

export class RentalService {
	
private new_rentalUrl = "http://127.0.0.1:8000/api/new-rental";
private rentalsUrl = "http://127.0.0.1:8000/api/rentals";
private  rentalUrl = "http://127.0.0.1:8000/api/rental";
private new_equipmentUrl = "http://127.0.0.1:8000/api/new-equipment";
private rental_equipmentUrl = "http://127.0.0.1:8000/api/rental-equipment";
private uploadUrl = "http://127.0.0.1:8000/api/upload-rental";

constructor (private http : Http) {}

 new_rental(rental) { 
	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
   return this.http.post(this.new_rentalUrl,JSON.stringify(rental),options)  
	.map((response : Response) => response.json());;
 }

rentals () {
	return   this.http.get(this.rentalsUrl)
   .map((response : Response) => response.json());
}

 rental (id) { 
 return   this.http.get(this.rental_equipmentUrl+'/'+id)
   .map((response : Response) => response.json());
 }

rental_equipment (data) {
 	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.rental_equipmentUrl,JSON.stringify(data),options)
	   .map((response:Response) => response.json());
} 


 new_equipment (detail) {
	 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_equipmentUrl,JSON.stringify(detail),options)
	 .map((response:Response) => response.json());
}


 upload (id,data) {
	 return this.http.post(this.uploadUrl+'/'+id,data)	
 } 



 
} 


