import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import {MatButtonModule} from '@angular/material/button';
import {MatDialogModule} from '@angular/material/dialog';
import {MatSelectModule} from '@angular/material/select';

import { AppRouting } from './app.routing';
import { ViewModule } from './view/view.module';
import { HttpModule } from '@angular/http';
import {CommonModule} from '@angular/common'; 
import { ReactiveFormsModule,FormsModule } from '@angular/forms';
import { EntryModule } from './entry/entry.module';

import { ViewService } from './view/view.service';
import { ProductService } from './view/products/product.service';
import { SupplierService } from './view/suppliers/supplier.service';
import { ManufacturerService } from './view/manufacturers/manufacturer.service';
import { ProfessionalService } from './view/professionals/professional.service';
import { RentalService } from './view/rentals/rental.service';

import { AppComponent } from './app.component';
import { CarouselModule } from 'ngx-bootstrap/carousel';



@NgModule({
  declarations: [
    AppComponent],
  imports: [
    BrowserModule,BrowserAnimationsModule,AppRouting,CarouselModule.forRoot(),HttpModule,MatDialogModule,CommonModule,ReactiveFormsModule,FormsModule,ViewModule,EntryModule],
  providers: [ViewService,ProductService,SupplierService,ManufacturerService,ProfessionalService,RentalService],
  bootstrap: [AppComponent]
})
export class AppModule { }



