import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
@Component({
  selector: 'fill',
   template: `

<div class="row">
<div class="col-md-11 main">

<h4> Unit Rate Formula </h4>

</div>
</div>

<div class="row">
<form [formGroup]="BlockForm" class="form-horizontal" (ngSubmit)="solution()">  
<div class="col-md-7 insert">

 <div class="form-group">
    <label  class="col-sm-6 control-label">Govrt Basic Salary</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="govt">
    </div>
   </div>

     <div class="form-group">
    <label class="col-sm-6 control-label">Cost of Tools</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="tools">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Persnaldays required </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="days">
    </div>
   </div>
   
    <div class="form-group">
    <label  class="col-sm-6 control-label"> Murram</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="murram">
    </div>
    </div>

     <div class="form-group">
    <label  class="col-sm-6 control-label"> Tipper</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="tipper">
    </div>
    </div>

         <div class="form-group">
    <label  class="col-sm-6 control-label"> Roller </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="roller">
    </div>
    </div>



     <div class="form-group">
    <label  class="col-sm-6 control-label"> Overhead Expenses</label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="overhead">
    </div>
    </div>
    
     <div class="form-group">
    <label  class="col-sm-6 control-label">Profit </label>
    <div class="col-sm-3">
      <input type="integer" class="form-control" formControlName="profit">
    </div>
   </div>
   



</div>	

<div class="col-md-5 answer">

<p>  <button type="submit" class="btn btn-primary">CALCULATE</button></p>
<p> Unit Rate Per metre cubic </p>
<p> {{rate}}

</div>
</form>
</div>




   `,
  styleUrls: ['./earth.component.css']
})
export class FillComponent implements OnInit {

  constructor() { }

BlockForm = new FormGroup ({
  govt : new FormControl (),
  tools : new FormControl (),
  murram : new FormControl (),
  days : new FormControl (),
  overhead : new FormControl (),
  tipper : new FormControl (),
    roller : new FormControl (),
  profit : new FormControl ()

})

  ngOnInit() {
  } 

  labour:number;
cost:number;
replacement:number;
sub_one:number;
tools:number;
days :number;
incidentals:number;
mandays:number;
cost_tools:number;
sub_two:number;
sub_three:number;
sub_four:number;
tipper:number;
roller:number;
murram:number;
compation:number;
overhead:number;
profit:number;
rate:number;

solution () {
 
let g = this.BlockForm.value.govt;
let t = this.BlockForm.value.tools;
let d = this.BlockForm.value.days;
let tip = this.BlockForm.value.tipper;
let r = this.BlockForm.value.roller;
let m = this.BlockForm.value.murram;
let o = this.BlockForm.value.overhead/100;
let p = this.BlockForm.value.profit/100;



this.labour = (1.81 * g/25) * 0.888888889;
this.cost  = t;
this.replacement = this.cost * 0.1;
this.sub_one = +this.cost + +this.replacement;
this.tools = 0.5 * this.sub_one;
this.days = d;
this.incidentals = 0.15 * this.days;
this.mandays = +this.days + +this.incidentals;
this.cost_tools = (this.tools / this.mandays ) * 0.888888889;
this.sub_two = +this.labour + +this.cost_tools;
this.tipper = tip;
this.murram = m;
this.sub_three = +this.sub_two + +this.murram + +this.tipper;
this.compation = 0.2 * this.sub_three;
this.roller = r; 
this.sub_four  = +this.sub_three + +this.compation + +this.roller;
this.overhead = o * this.sub_four;
this.profit = p * this.sub_four;

this.rate = (+this.sub_four + +this.overhead + +this.profit) / 4
console.log (this.rate)
}

}



