import { Component, OnInit } from '@angular/core';
import { ProductService } from './product.service';
import { ActivatedRoute,Router } from '@angular/router';


@Component({
  selector: 'products',
  template: `


<div class="container">
<div class="row intro">
<div class="col-md-12">
<ol class="breadcrumb">
  <li><a href="#">Home></a></li>
  <li><a href="#">product categories></a></li>
  <li class="active">Paints</li>
</ol>
</div>
</div>

<div class="row">
<div class="col-md-2">

</div> 

<div class="col-md-10">
<table class="table"> 
  <tr>
    <th>  Image </th>
    <th> Sub Category </th> 
 <th> size </th>
 <th> price </th> 

     <th> Company Name </th> <th> Status </th>
  </tr>

  <tr *ngFor="let product of products">
    <td> <img [src]="'http://localhost:8000/images/'+product.logo"> </td>
     <td>{{product.product_name}} </td> 
      <td>{{product.size}} </td> 
      <td> {{product.price}}</td>  
      <td><a [routerLink]="['/company',product.company_name]" routerLinkActive="active">{{product.company_name}}</a> </td>
      <td> {{product.type_name}} <td>
  </tr>

</table>
</div>
</div>
</div>

  

  `,
  styleUrls: ['./product.component.css']
})

export class ProductComponent implements OnInit {

  constructor(private api : ProductService,private route : ActivatedRoute) { }

 id = this.route.snapshot.paramMap.get('id');


  ngOnInit() {
  
   this.route.data
      .subscribe ((data => {
         this.products = data.res
       console.log(data.res);
         }));
  }

products = [];


}



