import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable ()

export class NavService {  

private pros = 11;

constructor (private http : Http) { }

private get_categoryUrl = "http://127.0.0.1:8000/api/categories";
private get_pro_categoryUrl = "http://127.0.0.1:8000/api/pro-category";

private professionalURL = "http://127.0.0.1:8000/api/professionals";

private manuURL = "http://127.0.0.1:8000/api/companies_manu";
private supplierURL = "http://127.0.0.1:8000/api/companies_supplier";

 
private agentsURL = "http://127.0.0.1:8000/api/agent_regions";
private rentalsUrl = "http://127.0.0.1:8000/api/types-equip";
  
private search_agentUrl = "http://127.0.0.1:8000/api/agent-search";
private search_supplierUrl = "http://127.0.0.1:8000/api/search_supplier";
private search_manuUrl = "http://127.0.0.1:8000/api/search_manu";
private search_productUrl = "http://127.0.0.1:8000/api/search_product";

private fundiUrl = "http://127.0.0.1:8000/api/fundi";
 


search_agent(data) {
   return this.http.get(this.search_agentUrl+'/'+data)
   .map((response:Response) => response.json())
}


 get_Categorys () {
  return this.http.get(this.get_categoryUrl)
   .map((response:Response) => response.json());
 }

 professional () {
 return this.http.get(this.get_pro_categoryUrl)
   .map((response:Response) => response.json())
 }

manufactures () {
	return this.http.get(this.manuURL)
	.map((response:Response) => response.json())
}

suppliers () {
		return this.http.get(this.supplierURL)
	.map((response:Response) => response.json())
 }

agents () {
	return this.http.get(this.agentsURL)
	.map((response:Response) => response.json())
}

get_rental () {
    return this.http.get(this.rentalsUrl)
	.map((response:Response) => response.json())	
}



get_fundi () {
 return this.http.get(this.fundiUrl)
 .map((response:Response) => response.json());
}

}


