import { Component,OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import { FormGroup,FormControl } from '@angular/forms';
import { RentalService } from './rental.service';

@Component ({
	
	selector:'rental-equipment',
	template:`

<div class="container">
<div class="row">
<div class="col-md-8">
    
   
    <form [formGroup]="EquipmentForm" (ngSubmit)="onSubmit()"> 
  
  

    <div class="form-group">
    <label>Equipment Name</label>
    <input type="text" class="form-control" placeholder="Company Name" formControlName="name">
  </div>
  
    <div class="form-group">
    <label>specification</label>
    <input type="text" class="form-control" placeholder="Company Name" formControlName="specific">
  </div>


    <div class="form-group">
    <label>Rental per Month </label>
    <input type="text" class="form-control" placeholder="month" formControlName="month">
  </div>


    <div class="form-group">
    <label>Rental per Week </label>
    <input type="text" class="form-control" placeholder="week" formControlName="week">
  </div>


    <div class="form-group">
    <label>Rental per Day</label>
    <input type="text" class="form-control" placeholder="day" formControlName="day">
  </div>




    <div class="form-group">
    <label>Equipment Code</label>
    <input type="text" class="form-control" placeholder="code" formControlName="code">
  </div>



 <div class="form-group">
       <input type="submit" value="Register">
      </div>


</form>

   </div>
</div>
</div>


	`,
	styleUrls:['./rental.component.css']
})

export class EquipmentRentalComponent {
	

	ngOnInit ()  {

	}

constructor (private api : RentalService, private route : ActivatedRoute) { }


 EquipmentForm = new FormGroup ({

  name : new FormControl (),
  specific : new FormControl (),
  month : new FormControl (),
  week : new FormControl(),
  day : new FormControl (),
  hour : new FormControl (),
  code : new FormControl ()

})

onSubmit () {
 
  console.log(this.EquipmentForm.value)
  this.api.new_equipment(this.EquipmentForm.value)
  .subscribe (res => { 
   const equip_id = res;
  const rental_id = this.route.snapshot.paramMap.get('id');

 let detail = {rid:rental_id,eid:equip_id};
console.log(equip_id,rental_id)
   this.api.rental_equipment(detail)
   .subscribe(res => {})

 })}

}



