import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable ()
 
export class ProductService { 

 constructor (private http : Http) { }

private get_categoryUrl = "http://127.0.0.1:8000/api/product-categories";
private get_subcategoryUrl = "http://127.0.0.1:8000/api/category-sub";
private suppliersURL = "http://127.0.0.1:8000/api/suppliers";
private AgenciesURL = "http://127.0.0.1:8000/api/agents";
private manufacturesURL = "http://127.0.0.1:8000/api/manufacturers"; 

private agent_locationUrl = "http://127.0.0.1:8000/api/agent_regions";
private supplier_locationUrl = "http://127.0.0.1:8000/api/supplier_regions";
private manufacturer_locationUrl = "http://127.0.0.1:8000/api/manufacturer_regions";

private NewAgencyStoreURL = "http://127.0.0.1:8000/api/agent-store";
private new_manufactureURL = "http://127.0.0.1:8000/api/manufacturer-store";
private new_supplierURL = "http://127.0.0.1:8000/api/new_supplier";

private new_productURL = "http://127.0.0.1:8000/api/product-store"; 

private agent_productURL = "http://127.0.0.1:8000/api/agent-product"; 
private manu_productURL = "http://127.0.0.1:8000/api/manu-product"; 
private supplier_productURL = "http://127.0.0.1:8000/api/supplier-product"; 

private upload_agentUrl = "http://127.0.0.1:8000/api/upload-agent";
private upload_manuUrl = "http://127.0.0.1:8000/api/upload-manu";
private upload_supplierUrl = "http://127.0.0.1:8000/api/upload-supplier";



agent_location () {
   return this.http.get(this.agent_locationUrl)
   .map((response:Response) => response.json());
}

supplier_location () {
   return this.http.get(this.supplier_locationUrl)
   .map((response:Response) => response.json());
}

manufacturer_location () {
   return this.http.get(this.manufacturer_locationUrl)
   .map((response:Response) => response.json());
}

new_agency (agency) {
	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
 return this.http.post(this.NewAgencyStoreURL,JSON.stringify(agency),options)
 .map((response:Response) => response.json());
}

upload_agent (id,data) {
	 return this.http.post(this.upload_agentUrl+'/'+id,data)	
} 


upload_manu (id,data) {
	 return this.http.post(this.upload_manuUrl+'/'+id,data)	
} 

upload_supplier (id,data) {
	 return this.http.post(this.upload_supplierUrl+'/'+id,data)	
} 

new_product (product) {
	 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_productURL,JSON.stringify(product),options)
	 .map((response:Response) => response.json());
}


new_manufacture (manu) {

	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_manufactureURL,JSON.stringify(manu),options)
   .map((response:Response) => response.json());
 }

new_supplier(supplier) {

	let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.new_supplierURL,JSON.stringify(supplier),options)
	   .map((response:Response) => response.json());
 }



 agent_product (detail) {
	 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.agent_productURL,JSON.stringify(detail),options)
	 .map((response:Response) => response.json());
}


 manu_product (detail) {
	 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.manu_productURL,JSON.stringify(detail),options)
	 .map((response:Response) => response.json());
}

 supplier_product (detail) {
	 let headers = new Headers ({ 'Content-type': 'application/json'});
    let options = new RequestOptions({headers:headers});
	return this.http.post(this.supplier_productURL,JSON.stringify(detail),options)
	 .map((response:Response) => response.json());
}

 getCategory () {
  return this.http.get(this.get_categoryUrl)
   .map((response:Response) => response.json());
 }

  getSubCategory (data) {
  return this.http.get(this.get_subcategoryUrl+'/'+data)
   .map((response:Response) => response.json());
 }

suppliers () {
	return this.http.get(this.suppliersURL)
	.map((response:Response) => response.json());
}

agencies () {
	 return this.http.get(this.AgenciesURL)
   .map((response:Response) => response.json());

}

manufactures () {
	return this.http.get(this.manufacturesURL)
	.map((response:Response) => response.json());
}

}


