import { Component, OnInit } from '@angular/core';
import { RentalService } from './rental.service';
import { ActivatedRoute,Router } from '@angular/router';


@Component ({
	selector:'logo',
	template:`

   <label>Logo Company</label>
    <input type="file" (change)="selected($event)">
    <input type="submit" value="upload" (click)="upload()">
 
	`,
	styleUrls:['./rental.component.css']
})

export class RentalLogoComponent implements OnInit {
  
  logo : File = null;

  constructor(private api : RentalService, private route : ActivatedRoute,private router : Router) { }

  id = this.route.snapshot.paramMap.get('id');

  ngOnInit() {
  }

  selected (event) {
    this.logo= event.target.files[0]; 
  }

 upload () {

      console.log(this.logo)

      let formData : FormData = new FormData(); 
       formData.append('Logo', this.logo, this.logo.name);  
       this.api.upload(this.id,formData)
     .subscribe(res => { 

if (this.id) {

  this.router.navigate(['/rental/equipment',this.id]);

 } else { console.log('no access')
       

     }

 })

}
} 


