import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminComponent } from './admin.component';
import { FormOneComponent } from './invoices/form_1.component';
import { FormTwoComponent } from './invoices/form_2.component';
import { GenerateComponent } from './invoices/generate.component';

const AdminRoutes: Routes = [ 
{path:'', component:AdminComponent,
   children: [
    {path:'',component:FormOneComponent },
    {path:'generate/:id', component:FormTwoComponent},
    {path:'pdf/:id' ,component:GenerateComponent}
   ]
}

];

@NgModule ({
	
   imports : [RouterModule.forChild(AdminRoutes) ],
   exports : [RouterModule]

})

export class AdminRouting { }


