 import { Component, OnInit } from '@angular/core';
 import { FormGroup,FormControl } from '@angular/forms';

@Component({
  selector: 'reinforce',
  templateUrl: './reinforce.component.html',
  styleUrls: ['./reinforce.component.css']
})
export class ReinforceComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }


FormulaForm = new FormGroup ({
  ton  : new FormControl (),
  bind : new FormControl (),
  wage : new FormControl (),
  overhead : new FormControl (),
  transportation : new FormControl (),
  profit : new FormControl ()

})


data : any;
wastage:number;
bind : number; 
total_one:number;
total_two:number;
total_three:number;
transport:number;
fix:number;
bind_two:number;
unskilled:number;
profit:number;
overhead:number;
rate:number;


 
onSubmit ( ) { 
  this.data = this.FormulaForm.value;
let x = this.data.ton;
let y = this.data.bind;
let w = this.data.wage;
let o = this.data.overhead/100;
let p = this.data.profit/100;
let t = this.data.transportation/100;

this.wastage = 0.05 * +x;
this.bind = 10 * +y;
this.total_one = +x + +this.wastage + +this.bind;
this.transport = +t * +this.total_one;
this.total_two = +this.total_one + +this.transport;
this.fix = 1.81 * 2 * (+w / 25) * 7.5;
 this.unskilled = +this.fix;
this.total_three = +this.total_two + +this.fix + +this.unskilled;
this.overhead = o * +this.total_three;
this.profit = p * +this.total_three;

this.rate = (+this.total_three + +this.overhead + +this.profit) / 1000;
console.log(this.rate)

}


}



