import { Injectable } from '@angular/core';
import { Http,Response,Headers,RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable () 
 
export class ProfessionalService {
	
constructor (private http : Http) { } 

private categoryUrl ="http://127.0.0.1:8000/api/companies";


pro_category () {
	return this.http.get(this.categoryUrl)
	 .map ((response:Response) => response.json())
}



filter_category (name) {
	  return this.pro_category()
   .map(cate => cate.filter(res => res.professional_category === name));
} 

filter_sub (name) {
	  return this.pro_category()
   .map(sub => sub.filter(res => res.professional_name === name));
} 


}


