import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import {CommonModule} from '@angular/common';
import { ReactiveFormsModule,FormsModule } from '@angular/forms';
import { RentalRouting } from './rental.routing';
import { RentalComponent } from './rental.component';
import { RentalIndexComponent }  from './rental_index.component';
import { NewRentalComponent } from './new_rental.component';
import { EquipmentRentalComponent } from './equipment_rental.component';
import { RentalsComponent } from './rentals.component';
import { RentalLogoComponent } from './rental_logo.component';

@NgModule ({
	declarations:[RentalComponent,RentalIndexComponent,NewRentalComponent,EquipmentRentalComponent,RentalsComponent,RentalLogoComponent],
	imports:[HttpModule,CommonModule,RentalRouting,ReactiveFormsModule,FormsModule],
	exports:[]
})

export class RentalModule { }  


