import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
import { CraftmanService } from './craftman.service';
 import { ActivatedRoute,Router } from '@angular/router';

@Component({
  selector: 'professional',
  template: `
  <div class="container">
<div class="row">
<div class="col-md-12 wrap">

 <label>Company</label>
<input type="file" (change)="upload($event)">

</div> 
</div>
</div>

  `,
  styleUrls: ['./craftman.component.css']
})

export class ProfessionalComponent implements OnInit {

constructor(private api : CraftmanService, private route : ActivatedRoute) { }

 ngOnInit() {
  }

 id = this.route.snapshot.paramMap.get('id');

upload (event) {
	let file :File = null;
file = event.target.files[0];

let formData : FormData = new FormData();
 formData.append('Logo', file);

console.log(file.name)


this.api.professional_image (this.id,formData)
.subscribe (res => { })



}

}


